<template>
  <div class="tabbar">
    <van-tabbar
      v-model="active"
      active-color="#ee0a24"
      inactive-color="#000"
      v-if="this.$route.meta.show"
    >
      <van-tabbar-item v-for="(item,index) in icon" :key="index" :to="item.to">
        <span>{{item.text}}</span>
        <template #icon="props">
          <img :src="props.active ? item.active : item.inactive" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
      icon: [
        {
          inactive: require('../../assets/images/Home.svg'),
          active: require('../../assets/images/Home-active.svg'),
          text: '首页',
          to: '/',
        },
        {
          inactive: require('../../assets/images/video.svg'),
          active: require('../../assets/images/video-active.svg'),
          text: '视频',
          to: '/mv',
        },
        {
          inactive: require('../../assets/images/my.svg'),
          active: require('../../assets/images/my-active.svg'),
          text: '我的',
          to: '/my',
        },
      ],
    }
  },
  mounted() {},
  methods: {},
  watch: {
    '$route.path': function (newVal) {
      switch (newVal) {
        case '/':
          this.active = 0
          break
        case '/mv':
          this.active = 1
          break
        case '/my':
          this.active = 2
          break
      }
    },
  },
}
</script>

